<template>
  <div class="list breadList">
    <!--  v-show="state.searchState.show"-->
    <div class="searchData">
      <div class="listDataPath">
        <div class="pull-left">
          <a>
            <img :src="require('@/assets/img/u24.png')" alt="">
            <span></span>
          </a>
          <a>
            <img :src="require('@/assets/img/u27.png')" alt="">
            <span></span>
          </a>
          <a>
            <img :src="require('@/assets/img/u33.png')" alt="">
            <span></span>
          </a>
          <a>
            <img :src="require('@/assets/img/u39.png')" alt="">
            <span></span>
          </a>
          <a>
            <img :src="require('@/assets/img/u30.png')" alt="">
            <span></span>
          </a>
          <a>
            <img :src="require('@/assets/img/u36.png')" alt="">
            <span></span>
          </a>
          <a>
            <img :src="require('@/assets/img/u161.png')" alt="">
            <span></span>
          </a>
        </div>
        <div class="pull-right">
          <a style="background: #00ccff;color: #fff">
            <img :src="require('@/assets/img/u48.png')" alt="">
            <span></span>
          </a>
          <a>
            <img :src="require('@/assets/img/u44.png')" alt="">
            <span></span>
          </a>
        </div>
      </div>
      <div class="listHead">
        <h4 class="pull-center">
          
        </h4>
      </div>
      <Form :label-width="80" inline>
        <div class="clearfix xsAuto" style="padding:10px;text-align: center">
          <Row class="listFormInput">
            <Col span="8">
            <Form-item label="">
              <Input v-model="formItem.input" placeholder="" style="width:187px"></Input>
            </Form-item>
            </Col>
            <Col span="13">
            <Form-item label="" class="peopleInput">
              <Select v-model="params.category" placeholder="" style="width:480px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </Form-item>
            </Col>
            <Col span="3">
            <Form-item label="" class="nameInput">
              <Input v-model="formItem.input" placeholder="" style="width:54px"></Input>
            </Form-item>
            </Col>
            <Col span="8">
            <Form-item label="">
              <Select v-model="params.category" placeholder="" style="width:187px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </Form-item>
            </Col>
            <Col span="8">
            <Form-item label="">
              <Date-picker  type="date" placeholder="" style="width: 187px"></Date-picker>
            </Form-item>
            </Col>
            <Col span="8" >
            <Form-item label="">
              <Select v-model="params.category" placeholder="" style="width:187px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </Form-item>
            </Col>
            <Col span="8">
            <Form-item label="">
              <Select v-model="params.category" placeholder="" style="width:187px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </Form-item>
            </Col>
            <Col span="8">
            <Form-item label="">
              <Select v-model="params.category" placeholder="" style="width:187px">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </Form-item>
            </Col>
            <Col span="8">
            <Form-item label="">
              <Input v-model="formItem.input" placeholder="" style="width:187px"></Input>
            </Form-item>
            </Col>
          </Row>
        </div>
      </Form>
    </div>
    <!--  /-->
    <div class="listData">
      <Table :loading="loading2"   :size="tableSize"
             :data="listData"
             :border="true"
             :columns="columns1" ref="table" @on-select="onSelect" @on-selection-change="onSelectionChange"></Table>
      <!--<Page :total="100" show-sizer show-elevator @on-change="pageChange" style="margin-top: 10px"-->
      <!--@on-page-size-change="PageSizeChange"></Page>-->
    </div>

    <!--  -->
    <Modal v-model="deleteTip" width="360">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span></span>
      </p>
      <div style="text-align:center">
        <p>{{selection.length}},</p>
        <p></p>
      </div>
      <div slot="footer">
        <Button type="error" size="large" long @click="deleteBatch"></Button>
      </div>
    </Modal>
    <!-- / -->
    <!--  -->
    <Modal v-model="editModal" v-if='DateReady'>
      <p slot="header" style="text-align:center">
        <Icon type="information-circled"></Icon>
        <span v-if="currIndex==-1"></span>
        <span v-if="currIndex!=-1"></span>
      </p>
      <div style="text-align:center">
        <Form :model="formItem" :label-width="80">
          <Form-item label="">
            <Input v-model="currDate.who" placeholder=""></Input>
          </Form-item>
          <Form-item label="">
            <Date-picker type="date" placeholder="" style="width:100%" v-model="currDate.createdAt"
                         :editable='false'></Date-picker>
          </Form-item>
          <Form-item label="">
            <Date-picker type="date" placeholder="" style="width:100%" v-model="currDate.publishedAt"
                         :editable='false'></Date-picker>
          </Form-item>
          <Form-item label="">
            <Select v-model="currDate.type" placeholder="">
              <Option value="Android">Android</Option>
              <Option value="iOS">iOS</Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value="App">App</Option>
            </Select>
          </Form-item>

          <Form-item label="">
            <Input v-model="currDate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                   placeholder="..."></Input>
          </Form-item>
        </Form>
      </div>
      <div slot="footer">
        <Button type="success" size="large" long @click.native="saveBatch" :loading="loading" :disabled="saveDisabled">
          
        </Button>
      </div>
    </Modal>
    <!-- / -->
  </div>
</template>
<script>
  import elementResizeDetectorMaker from 'element-resize-detector'
  var erd = elementResizeDetectorMaker()
  export default {
    name: 'list',
    components: {},
    data () {
      return {
        formItem: {
          input: '',
          select: '',
          date: '',
          time: '',
          radio: '',
          checkbox: []
        },
        editModal: false,
        detailModal: false,
        deleteTip: false,
        loading2: false, // loading
        tableSize: 'default', // @:size
        DateReady: false, // 
        loading: false, // save
        currDate: {}, // 
        currIndex: 0, // 
        saveDisabled: false,
        params: {
          page: 1,
          limit: 10,
          category: 'Android'
        },
        selection: [], // 
        listData: [], // @:data
        columns1: [
          { // @:columns
            type: 'index', // 
            width: 60,
            align: 'center',
            title: ''
          },
          {
            title: '',
            key: 'who'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'desc',
            className: 'min-width'
          }
        ]
      }
    },
    watch: {
      /**
       * @params 
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      },
      fixedHeader: {
        handler (val) {
          if (val) {
            this.$Message.info('')
          }
        }
      },
      currDate: {
        handler (val) {
          for (let i = 0; i < Object.values(val).length; i++) {
            if (Object.values(val)[i] === '') {
              this.saveDisabled = true
              return
            } else {
              this.saveDisabled = false
            }
          }
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      searchShow () {
        this.searchState = !this.searchState
      },
      /**
       * 
       * */
      refresh () {
        this.getData(this.params)
      },
      /**
       * @params:category  page limit:
       * */
      getData (params) {
        this.loading2 = false
        this.listData = []
//        this.$api.orderList(params).then((res) => {
//          if (!res.error) {
//            this.listData = res.results
//            this.listData = []
//            this.DateReady = true
//            this.loading2 = false
//          } else {
//            this.$Message.error(res.msg)
//            this.loading2 = false
//          }
//        })
      },
      /**
       * @on-change 
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 
       * @show 
       * @remove 
       * @edit
       */
      show (index) {
        this.currIndex = index
        this.currDate = this.listData[index]
        this.detailModal = true
        this.$Modal.info({
          title: '',
          content: `${this.listData[index].who}<br>${this.listData[index].type}<br>${this.listData[index].desc}`
        })
      },
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
        this.editModal = true
        this.currIndex = index
        if (index === -1) { // 
          this.currDate = {
            createdAt: '',
            desc: '',
            publishedAt: '',
            type: '',
            used: true,
            who: '',
            url: 'c.fwone.com'
          }
        } else { // 
          this.currDate = this.listData[index]
        }
      },
      /**
       * 
       */
      deleteBatch () {
        this.deleteTip = false
        // ...
      },
      saveBatch () {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.$Message.info('')
          this.editModal = false
        }, 3000)
      },
      /**
       * 
       * @ type 1  2
       */
      exportData (type) {
        if (type === 1) {
          this.$refs.table.exportCsv({
            filename: ''
          })
        } else if (type === 2) {
          this.$refs.table.exportCsv({
            filename: '',
            original: false
          })
        }
      },
      /**
       * 
       * selection row
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
      erd.listenTo(window, 'resize', this.handleResize)
    }
  }
</script>
<style lang="less" >
  .peopleInput .ivu-form-item-content{
    margin-left: 40px !important;
  }
  .nameInput .ivu-form-item-content{
    margin-left: 20px !important;
  }
</style>



